// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueRouter from 'vue-router'
import $ from 'jquery'
import VueHtml5Editor from 'vue-html5-editor'
import VueScroller from 'vue-scroller'
import Util from '@/util/util'
import {ttf} from '@/util/ttf'
import Vant from 'vant';
import 'vant/lib/vant-css/index.css';
import VueLazyload from 'vue-lazyload'

Vue.use(VueHtml5Editor, {
    // new VueHtml5Editor(options) 
    name: "vue-html5-editor",
    // 
    showModuleName: false,
    // 
    image: {
        //   max file size
        sizeLimit: 512 * 1024,
        // ,base64
        upload: {
            //url: 'http://121.14.27.27:88/wp-json/wp/v2/media',
			url: null,
            headers: {
//          		'Authorization':'Bearer '+JSON.parse(localStorage.user).token
            },
            params: {},
            fieldName: 'file'
        },
        // ,localResizeIMG,null
        compress: {
            width: 320,
            height: 200,
            quality: 80
        },
        // ,
        uploadHandler(responseText){
            //default accept json data like  {ok:false,msg:"unexpected"} or {ok:true,data:"image url"}
            var json = JSON.parse(responseText)
            if (!json.ok) {
                alert(json.msg)
            } else {
                return json.data
            }
        }
    },
    // en-uszh-cn
    language: "zh-cn",
    // 
    hiddenModules: [],
    // 
    // keep only the modules you want and customize the order.
    // can be used with hiddenModules together
    visibleModules: [
        "text",
        "color",
        "font",
        "align",
        "list",
        "link",
        "unlink",
        "tabulation",
        "image",
        "hr",
        "eraser",
        "undo",
        "full-screen",
    ],
    // examples
    modules: {
        //omit,reference to source code of build-in modules
    }
})
Vue.use(VueRouter)
Vue.use(VueScroller)
Vue.use(Util)
Vue.use(Vant);
Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: require('./assets/img.png'),
  loading: require('./assets/img.png'),
  attempt: 1
})
/**/
Vue.component('app-header',() => import('./components/header'))
/**/
Vue.component('app-nav', () => import('./components/nav'))
/**/
Vue.component('app-footer', function (resolve) {
  require(['./components/footer'], resolve)
})

new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: {
  	App
  }
})



